<template>
  <div>
    <cms-breadcrumb to="/product/virtualCard/list">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout the-error">
      <el-form ref="form" :model="formData" label-width="120px" :rules="rules">
        <el-form-item label="模板名称:" prop="name">
          <el-input maxlength="150" v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="使用网址:" prop="useWeb">
          <el-input maxlength="50" v-model="formData.useWeb"></el-input>
          <span class="gray">请输入带网络协议（https://或http://）的url</span>
        </el-form-item>
        <el-form-item label="卡密模板结构" prop="structure" style="margin-bottom:22px;">
          <div class="info-wrap" style="width:480px;border:1px dashed #999;padding:5px;">
            <table>
              <tr>
                <th>键</th>
                <th>显示名称</th>s
              </tr>
              <tr v-for="(item ,index) in structure" :key="index">
                <td style="height:40px;"><el-input type="text" maxlength="20" v-model="item.key" placeholder="请输入键值" 
                :disabled='index<2?true:false' style="margin:8px;max-width:180px;"></el-input></td>
                <td><el-input type="text" maxlength="20" v-model="item.label" required placeholder="请输入显示名称" style="margin:8px;;max-width:190px;"></el-input></td>
                <td><el-button  @click="removeRow(index)" v-if="index>=2?true:false" style="margin:8px;" size="mini" type="danger">删除</el-button></td>
              </tr>
            </table>
            <el-button @click="addRow" v-if="addBtn" type="primary" style="margin-left:5px;" size="mini">添加</el-button>
          </div>
        </el-form-item>
        <el-button @click="saveData" type="primary" style="margin:8px 0 0 130px;width:90px;">保存</el-button>
      </el-form>
    </div>
  </div>
</template>
<script>
import { productVirtualCardtplGet, productVirtualCardtplUpdate } from '@/api/product/product.js'
export default {
  data() {
    var validateJson = (rule, value, callback) => {
      let flag = true
      const keys = /[^a-zA-Z]/g
      for (let i = 0; i < this.structure.length; i++) {
        if (this.structure[i].label === '' || this.structure[i].key === '') {
          flag = false
          break
        } else {
          if (keys.test(this.structure[i].key)) {
            callback(new Error('键值请输入英文'))
            break
          }
        }
      }
      if (flag) {
        callback()
      } else {
        callback(new Error('请输入完整后再提交'))
      }
    }
    return {
      formData: {
        name: '',
        useWeb: ''
      },
      isShow: true,
      addBtn: true,
      structure: [
        {
          label: 'sku',
          key: ''
        }, {
          label: 'key',
          key: ''
        }
      ],
      rules: {
        'name': [
          { required: true, message: '请输入活动名称', trigger: 'blur' }
        ],
        'structure': [
          { required: true, validator: validateJson, trigger: ['blur', 'change'] }
        ]
      }

    }
  },
  watch: {
    structure: {
      handler() {
        var num = JSON.stringify(this.structure).length
        if (num >= 105) {
          this.addBtn = false
        } else {
          this.addBtn = true
        }
      },
      immediate: true,
      deep: true
    }
  },
  methods: {
    removeRow(index) {
      this.structure.splice(index, 1)
    },
    addRow() {
      this.structure.push({
        label: '',
        key: ''
      })
    },
    saveData() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          var params = {
            id: this.$route.query.id,
            name: this.formData.name,
            useWeb: this.formData.useWeb,
            strutContent: this.structure,
            storeId: ''
          }
          JSON.stringify(params.strutContent)
          productVirtualCardtplUpdate(params)
            .then(res => {
              if (res.code === 200) {
                this.$successMessage(res.message)
                this.$routerLink(('/product/virtualCard/list'), 'list')
              }
            })
        } else {
          return false
        }
      })
    },
    getCardtpl() {
      productVirtualCardtplGet({ id: this.$route.query.id })
        .then(res => {
          console.log(res.data)
          this.formData.name = res.data.name
          this.formData.useWeb = res.data.useWeb
          this.structure = JSON.parse(res.data.strutContent)
        })
    }
  },
  created() {
    this.getCardtpl()
  }
}
</script>
<style>
.cms-layout .the-error .el-form-item__error{
  top: 100% !important;
  left: 0 !important;
}
</style>








